@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@300;600;700&family=Raleway:wght@400;600;700&family=VT323&display=swap');

* {
    margin: 0;
    padding: 0;
    box-sizing: 0;
}

.experience {
    position: fixed;
    width: 100vw;
    height: 100vh;
    z-index: 0;

}

.experience-canvas {
    width: 100%;
    height: 100%;
}

div {
    font-family: 'VT323', monospace;
    font-size: larger;


}

#board {
    display: flex;
    justify-content: center;
    align-items: center;
}

#game-over {
    position: fixed;
    width: 100vw;
    height: 100vh;
    z-index: 1;
    background-image: url('./assets/img/glitch.gif');
    background-repeat: no-repeat;
    background-size: 100% 100%;
    opacity: 0.98;
}

.reborn {
    position: fixed;
    width: 100vw;
    height: 100vh;
    color: azure;
    font-size: 48px;
    top: 75%;
    left: 30%
}

.options {
    width: 15vw;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.option {
    width: 5vw;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.option:last-child {
    width: 4vw;
}


.arrow-right {
    width: 0;
    height: 0;
    border-top: 20px solid transparent;
    border-bottom: 20px solid transparent;
    border-left: 20px solid azure;
}

.snippet{
    position: fixed;
    width: 100vw;
    height: 100vh;
  z-index: 2;
    background-color: rgba(255, 214, 189, 0.9);
  

}

.dot-collision {
    position: fixed;
    top: 50%;
    left:50%;
    width: 50px;
    height: 50px;
    border-radius: 25px;
    background-color: #ff4882;
    color: #ff4882;
  }
  
  .dot-collision::before, .dot-collision::after {
    content: '';
    display: inline-block;
    position: absolute;
  }
  
  .dot-collision::before {
    left: -50px;
    width: 50px;
    height: 50px;
    border-radius: 25px;
    background-color: #ff4882;
    color: #ff4882;
    animation: dotCollisionBefore 2s infinite ease-in;
  }
  
  .dot-collision::after {

    left: 50px;
    width: 50px;
    height: 50px;
    border-radius: 25px;
    background-color: #ff4882;
    color: #ff4882;
    animation: dotCollisionAfter 2s infinite ease-in;
    animation-delay: 1s;
  }
  
  @keyframes dotCollisionBefore {
    0%,
    50%,
    75%,
    100% {
      transform: translateX(0);
    }
    25% {
      transform: translateX(-25px);
    }
  }
  
  @keyframes dotCollisionAfter {
    0%,
    50%,
    75%,
    100% {
      transform: translateX(0);
    }
    25% {
      transform: translateX(15px);
    }
  }




  
  .dot-flashing {
    position: fixed;
    top: 50%;
    left:50%;
    width: 50px;
    height: 50px;
    border-radius: 25px;
    background-color: #ff4882;
    color: #ff4882;
    animation: dotFlashing 1s infinite linear alternate;
    animation-delay: .5s;
  }
  
  .dot-flashing::before, .dot-flashing::after {
    content: '';
    display: inline-block;
    position: absolute;
    top: 0;
  }
  
  .dot-flashing::before {
    left: -75px;
    width: 50px;
    height: 50px;
    border-radius: 25px;
    background-color: #ff4882;
    color: #ff4882;
    animation: dotFlashing 1s infinite alternate;
    animation-delay: 0s;
  }
  
  .dot-flashing::after {
    left: 75px;
    width: 50px;
    height: 50px;
    border-radius: 25px;
    background-color: #ff4882;
    color: #ff4882;
    animation: dotFlashing 1s infinite alternate;
    animation-delay: 1s;
  }
  
  @keyframes dotFlashing {
    0% {
      background-color: #ff4882;
    }
    50%,
    100% {
      background-color: #f9cddc;
    }
  }
  

  .text{
    position: absolute;
    width: 75%;
    top: 25vh;
    left: 10vw;
    border: #ff4882 6px solid;
    padding: 3rem;
    font-size: 4rem;
    border-radius: 1.5rem;
    color: #ff4882;
  }
  #start{
    position: fixed;
    width: 100vw;
    height: 100vh;
  z-index: 3;
    background-color: rgba(255, 214, 189, 0.9);
  }

  #play-btn{
    position: relative;
    left:50%;
    margin-top: 1rem;
    background-color: #ff4882;
    color: azure;
    font-size: 2rem;
    padding: 1.5rem;
    border: rgba(255, 253, 253, 0) 0.5px solid;
    border-radius: 2.5rem;
    font-family: 'VT323', monospace;
  }
  #play-btn:hover{
    background-color: #c0305d;
  }
  #play-btn:active{
    background-color: #c0305d;
    transform: translateY(4px);

  }

  #end-game{
    position: fixed;
    width: 100vw;
    height: 100vh;
    background-color: #000000;

  }
  .end-text{
    position: fixed;
    color: white;
    font-size: 2.5rem;
    width: 75%;
    top: 25vh;
    left: 10vw;
    border: white 6px solid;
    padding: 3rem;
    font-size: 4rem;
    border-radius: 1.5rem;
  }
  .end-main {
    position: relative;
      background-image: linear-gradient(to left, violet, indigo, blue, green, yellow, violet, red);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
    margin-left: auto;
    margin-right: auto;
    color: white;
    font-size: 8.5rem;
    left: 25vw;
  }

